.games {
  position: relative;

  padding-top: vw(118);
  padding-bottom: vw(104);
}

.games__heading {
  margin-bottom: vw(66);
}

.card {
  width: vw(320);
  height: vw(550);

  border: vw(2) solid var(--color-blue);
  border-radius: vw(11);

  margin: 0 vw(20);
}

.card__img {
  width: vw(320);
  height: vw(340);

  align-self: center;
}

.card__heading {
  padding: vw(21) vw(16) 0 vw(16);
  margin-bottom: vw(9);
}

.card__desc {
  padding: 0 vw(16);
}

.card__more {
  display: inline-block;
  margin: vw(-6) vw(16) vw(20) vw(155);

  font-weight: 500;
  font-size: vw(16);
  line-height: vw(19);
  color: var(--color-blue);
}

.swiper-container {
  margin-left: vw(303);
  margin-bottom: vw(64);
}

.swiper-button-prev,
.swiper-button-next {
  position: absolute;
  left: vw(141);
  width: vw(56);
  height: vw(56);
  border-radius: 100%;
  background: var(--color-white);
  border: vw(2) solid var(--color-blue);
}

.swiper-button-prev.swiper-button-disabled,
.swiper-button-next.swiper-button-disabled {
  opacity: .4;
  cursor: not-allowed;
  pointer-events: auto;
}

.swiper-button-prev::after,
.swiper-button-next::after {
  content: '';
  width: vw(56);
  height: vw(56);
  border-radius: 100%;
}

.swiper-button-prev::before,
.swiper-button-next::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-30%, -50%) rotate(-45deg);
  width: vw(20);
  height: vw(20);
  border-left: vw(2) solid var(--color-blue);
  border-top: vw(2) solid var(--color-blue);
}

.swiper-button-next::before {
  transform: translate(-70%, -50%) rotate(135deg);
}

.swiper-button-prev {
  top: vw(440);
}

.swiper-button-next {
  top: vw(528);
}

.games__btn {
  width: vw(320);
  height: vw(56);

  color: #000;
  border: vw(2) solid var(--color-blue);

  margin: 0 auto;
}