.header {
  width: 100%;
  height: vw(88);

  background: #FFFFFF;
  box-shadow: 0px 4px 25px rgba(#000, 0.1);
}

.header__container {
  display: flex;
  align-items: center;

  height: 100%;
}

.header__logo {
  width: vw(141.37);
  height: vw(60);

  margin-right: auto;

  &__svg {
    width: vw(141.37);
    height: vw(60);
  }
}

.nav {
  height: 100%;

  margin-right: auto;
  padding-right: vw(108);

  &__list {
    display: flex;
    align-items: center;

    height: 100%;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;

    height: 100%;

    .nav__link {
      display: flex;
      align-items: center;
      justify-content: center;

      width: 100%;
      height: 100%;

      font-weight: 500;
      font-size: vw(16);
      line-height: vw(19);
      color: #000000;
    }
  }

  &__item:not(:last-child) {
    margin-right: vw(32);
  }
}

.header__btn {
  margin-right: 0;
}