.preference {
  padding-top: vw(130);
  padding-bottom: vw(104);
}

.preference__container {
  width: 100%;
}

.preference__heading {
  margin-bottom: vw(66);
}

.preference__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  width: 100%;
}

.preference__item {
  width: vw(591); //557px
  height: vw(248);
  padding: vw(32) vw(16);

  background: var(--color-preference);
  border-radius: vw(8);

  &__svg {
    width: vw(40);
    height: vw(32);

    margin-bottom: vw(29);
  }

  &__low-heading {
    margin-bottom: vw(9);
  }

  &__low-desc {
    max-width: vw(559);
  }
}

.preference__item:nth-child(2) {
  width: vw(394);
}

.preference__item:nth-child(even),
.preference__item:nth-child(1) {
  margin-right: vw(132);
}

.preference__item:nth-child(-n+3) {
  margin-bottom: vw(64);
}