.steps {
  padding-top: vw(118);
  padding-bottom: vw(102);
}

.steps__heading {
  margin-bottom: vw(8);
}

.steps__desc {
  margin-bottom: vw(76);
}

.steps__list {
  display: flex;
  flex-direction: column;
  align-items: center;

  max-width: vw(1511);

  padding-right: vw(197);
  margin: 0 auto;

  box-sizing: content-box;
}

.steps__item {
  display: flex;
  align-items: center;
  justify-content: space-between;

  width: 100%;

  &__number {
    font-weight: 500;
    font-size: vw(40);
    line-height: vw(32);
    background: var(--gradient-main);
    -webkit-background-clip: text;
    color: transparent;
  }

  &__desc {
    width: vw(1314);

    padding: vw(28) 0;
    border-bottom: vw(2) solid var(--color-blue);
  }

  &:nth-child(1) &__desc {
    border-top: vw(2) solid var(--color-blue);
  }
}