.faq {
  padding-top: vw(118);
  padding-bottom: vw(102);
}

.faq__heading {
  margin-bottom: vw(66);
}

#accordion {
  border-bottom: vw(2) solid var(--color-blue);
}

.accordion__heading {
  position: relative;

  width: 100%;
  padding: vw(26) 0;
  border-top: vw(2) solid var(--color-blue);

  cursor: pointer;

  &::before,
  &::after {
    content: '';
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);

    transition: .3s;
  }

  &::before {
    width: vw(32);
    height: vw(32);
    border-radius: 100%;
    background: var(--gradient-main);
  }

  &::after {
    right: vw(7);
    width: vw(18);
    height: vw(18);
    background-image: url('../img/faq/plus.png');
    background-size: contain;
    background-repeat: no-repeat;
    z-index: 100;
    transform: translateY(-50%);
  }
}

.accordion__heading.ui-accordion-header-active::after {
  transform: translateY(-50%) rotate(45deg);
}

.accordion__desc {
  padding: 0 0 vw(26) 0;
}