.popup {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  width: 100%;
  height: 100%;

  z-index: 1000;

  transition: .5s;

  visibility: hidden;
  opacity: 0;
}

.popup.is_active {
  visibility: visible;
  opacity: 1;
}

.popup__bg {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: var(--color-black);
  opacity: .7;
}

.popup__card {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -40%);

  display: block;

  width: vw(423);
  height: vw(552);

  background: var(--gradient-popup);
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
  border-radius: vw(8);

  z-index: 100;

  transition: .5s;
}

.popup__card.not_visible {
  display: none;
}

.popup.is_active .popup__card {
  transform: translate(-50%, -50%);
} 

.popup__heading {
  max-width: vw(247);

  padding: vw(34) 0 vw(6) vw(32);

  line-height: 100%;
}

.popup__desc {
  max-width: vw(280);

  padding: 0 0 vw(33) vw(32);
}

.form__heading {
  font-weight: 500;
  font-size: vw(16);
  line-height: vw(24);
  letter-spacing: 0.02em;
  color: var(--color-white);

  padding: 0 0 vw(1) vw(32);
}

.form__input {
  width: vw(359);
  height: vw(48);

  padding: vw(12) vw(16);

  font-size: vw(18);
  line-height: vw(24);
  letter-spacing: 0.02em;
  color: var(--color-gray-popup);

  border: none;
  border-radius: vw(8);

  margin-left: vw(32);
  margin-bottom: vw(31);
}

.form__input::placeholder {
  font-size: vw(18);
  line-height: vw(24);
  letter-spacing: 0.02em;
  color: var(--color-gray-popup);
}

.form__btn {
  width: vw(359);
  height: vw(56);

  border: none;
  cursor: pointer;

  margin-top: vw(8);
  margin-left: vw(32);
  margin-bottom: vw(12);
}

.form__agree {
  display: inline-block;
  max-width: vw(227);

  font-size: vw(12);
  line-height: vw(16);
  color: var(--color-white);
  text-align: center;
  
  margin: 0 vw(98);
}

.popup__close {
  position: absolute;

  top: vw(16);
  right: vw(16);
}

.popup__close,
.popup__close svg {
  width: vw(14);
  height: vw(14);
}


.popup__thx {
  position: relative;
  top: 50%;
  left: 50%;
  
  display: block;
  
  width: vw(423);
  height: vw(208);
  
  background: var(--gradient-popup);
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
  border-radius: vw(8);
  
  z-index: 100;
  
  transform: translate(-50%, -50%);
  visibility: visible;
  opacity: 1;

  transition: .5s;
}

.popup__thx.not_visible {
  transform: translate(-50%, -25%);
  visibility: hidden;
  opacity: 0;
}