@charset "UTF-8";
:root {
  --color-main: #FF7A00;
  --color-blue: #00A9D0;
  --color-turquoise: #13EAC3;
  --color-white: #FFFFFF;
  --color-black: #000000;
  --color-gray: #626262;
  --color-gray-popup: #C4C4C4;
  --color-preference: #F7F7F7;
  --color-privacy: #868686;
  --gradient-main: radial-gradient(114.2% 361.14% at 0% 0%, #00A9D0 26.27%, #13EAC3 100%);
  --gradient-popup: radial-gradient(220.16% 129.28% at 0% 0%, #00A9D0 29.47%, #13EAC3 100%);
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Black.woff2") format("woff2"), url("../fonts/Roboto-Black.woff") format("woff");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Light.woff2") format("woff2"), url("../fonts/Roboto-Light.woff") format("woff");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Thin.woff2") format("woff2"), url("../fonts/Roboto-Thin.woff") format("woff");
  font-weight: 100;
  font-display: swap;
  font-style: normal;
}
