html {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

img {
  max-width: 100%;
}

button {
  border: none;
  cursor: pointer;
  background: transparent;
}

a {
  cursor: pointer;
}

.is_outer {
  position: relative !important;
  left: -9999px !important;
}

.is_hidden {
  display: none !important;
}

.for_mobile {
  display: none;
}
@media (max-width: 1024px) {
  .for_mobile {
    display: block;
  }
}

.error_ {
  border: 2px solid #ff4019 !important;
}

.visualy_hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  overflow: hidden;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 12.5vw;
  height: 2.9166666667vw;
  font-weight: 500;
  font-size: 1.0416666667vw;
  line-height: 1.25vw;
  border-radius: 0.4166666667vw;
  color: var(--color-white);
}

.btn_fill {
  background: var(--color-main);
  margin-right: 2.0833333333vw;
}

.btn_border {
  border: 0.15625vw solid var(--color-white);
}

.btn-box {
  display: flex;
  align-items: center;
}

.big-heading {
  font-weight: 700;
  font-size: 2.5vw;
  line-height: 3.3333333333vw;
  color: var(--color-white);
}

.big-desc {
  font-size: 0.9375vw;
  line-height: 1.25vw;
  color: var(--color-white);
}

.heading {
  font-weight: 700;
  font-size: 2.0833333333vw;
  line-height: 2.9166666667vw;
  color: var(--color-black);
}

.desc {
  font-weight: 400;
  font-size: 0.9375vw;
  line-height: 1.25vw;
  color: var(--color-gray);
}

.low-heading {
  font-weight: 500;
  font-size: 1.0416666667vw;
  line-height: 1.25vw;
  color: var(--color-black);
}

.low-desc {
  font-weight: 400;
  font-size: 0.9375vw;
  line-height: 1.25vw;
  color: var(--color-black);
}

.btn_fill {
  transition-property: background, color;
  transition-duration: 0.2s;
}

.btn_fill:hover,
.btn_fill:focus,
.btn_fill:active {
  background: transparent;
  border: 0.15625vw solid var(--color-main);
}

.header__btn:hover,
.header__btn:focus,
.header__btn:active {
  color: var(--color-black);
}

.btn_border {
  transition-property: background, color, border;
  transition-duration: 0.2s;
}

.btn_border:hover,
.btn_border:focus,
.btn_border:active {
  background: var(--color-white);
  color: var(--color-black);
}

.games__btn {
  transition-duration: 0.3s;
}

.games__btn:hover,
.games__btn:focus,
.games__btn:active {
  background: var(--color-blue);
  color: var(--color-white);
}

.header__container {
  overflow: hidden;
}

.nav__link {
  position: relative;
}

.nav__link::before {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 0.2083333333vw;
  background: var(--color-black);
  opacity: 0;
  transform: translateY(100%);
  transition: opacity 0.2s, transform 0.3s;
}

.nav__link:hover::before,
.nav__link:focus::before,
.nav__link:active::before {
  opacity: 1;
  transform: none;
}

.nav__link.is_active::before {
  opacity: 1;
  transform: none;
}

.preference__item {
  transition: background 0.5s, box-shadow 0.5s;
}

.preference__item:hover {
  background: transparent;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.04);
}

.accordion__heading:hover::after {
  transform: translateY(-50%) rotate(-180deg);
}

.swiper-button-prev::before,
.swiper-button-next::before {
  transition: 0.3s;
}

.swiper-button-prev:hover::before,
.swiper-button-next:hover::before {
  border-left: 0.1041666667vw solid var(--color-white);
  border-top: 0.1041666667vw solid var(--color-white);
}

.swiper-button-prev.swiper-button-disabled:hover::before,
.swiper-button-next.swiper-button-disabled:hover::before {
  border-left: 0.1041666667vw solid var(--color-blue);
  border-top: 0.1041666667vw solid var(--color-blue);
}

.swiper-button-prev,
.swiper-button-next {
  transition: 0.3s;
}

.swiper-button-prev:hover,
.swiper-button-next:hover {
  background: var(--color-blue);
}

.swiper-button-prev.swiper-button-disabled:hover,
.swiper-button-next.swiper-button-disabled:hover {
  background: var(--color-white);
}

.form__input {
  transition: 0.3s;
}

.form__input:focus,
.form__input:active {
  color: var(--color-black);
  outline: none;
}

.header *::selection,
.hero *::selection,
.trade *::selection {
  background: var(--color-main);
  color: var(--color-black);
}

.preference *::selection,
.steps *::selection,
.games *::selection,
.faq *::selection,
.footer *::selection {
  background: var(--color-turquoise);
  color: var(--color-black);
}

*::-webkit-scrollbar {
  width: 12px;
  /* ширина scrollbar */
}

*::-webkit-scrollbar-track {
  background: transparent;
  /* цвет дорожки */
}

*::-webkit-scrollbar-thumb {
  background: var(--color-main);
  /* цвет плашки */
  border-radius: 1.0416666667vw;
  /* закругления плашки */
  border: 0.15625vw solid var(--color-white);
  /* padding вокруг плашки */
}

.container {
  max-width: 97.3958333333vw;
  padding: 0 0.78125vw;
  margin: 0 auto;
}

.header {
  width: 100%;
  height: 4.5833333333vw;
  background: #FFFFFF;
  box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.1);
}

.header__container {
  display: flex;
  align-items: center;
  height: 100%;
}

.header__logo {
  width: 7.3630208333vw;
  height: 3.125vw;
  margin-right: auto;
}
.header__logo__svg {
  width: 7.3630208333vw;
  height: 3.125vw;
}

.nav {
  height: 100%;
  margin-right: auto;
  padding-right: 5.625vw;
}
.nav__list {
  display: flex;
  align-items: center;
  height: 100%;
}
.nav__item {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.nav__item .nav__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-weight: 500;
  font-size: 0.8333333333vw;
  line-height: 0.9895833333vw;
  color: #000000;
}
.nav__item:not(:last-child) {
  margin-right: 1.6666666667vw;
}

.header__btn {
  margin-right: 0;
}

.hero {
  position: relative;
  width: 100%;
  height: 41.40625vw;
}

.hero__bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 41.40625vw;
  z-index: -3;
}

.hero__gamepad {
  position: absolute;
  top: 0;
  right: 8.8541666667vw;
  width: 36.3020833333vw;
  height: 41.5104166667vw;
  z-index: -2;
}

.hero__gamepad_up {
  position: absolute;
  top: 0;
  right: 8.8541666667vw;
  width: 36.3020833333vw;
  height: 41.5104166667vw;
  z-index: 1;
}

.hero__bg-up {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 41.40625vw;
  z-index: -1;
}

.hero__heading {
  max-width: 31.8229166667vw;
  padding-top: 7.5vw;
  margin-bottom: 0.3125vw;
}

.hero__desc {
  max-width: 29.4791666667vw;
  margin-bottom: 3.8541666667vw;
}

.preference {
  padding-top: 6.7708333333vw;
  padding-bottom: 5.4166666667vw;
}

.preference__container {
  width: 100%;
}

.preference__heading {
  margin-bottom: 3.4375vw;
}

.preference__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.preference__item {
  width: 30.78125vw;
  height: 12.9166666667vw;
  padding: 1.6666666667vw 0.8333333333vw;
  background: var(--color-preference);
  border-radius: 0.4166666667vw;
}
.preference__item__svg {
  width: 2.0833333333vw;
  height: 1.6666666667vw;
  margin-bottom: 1.5104166667vw;
}
.preference__item__low-heading {
  margin-bottom: 0.46875vw;
}
.preference__item__low-desc {
  max-width: 29.1145833333vw;
}

.preference__item:nth-child(2) {
  width: 20.5208333333vw;
}

.preference__item:nth-child(even),
.preference__item:nth-child(1) {
  margin-right: 6.875vw;
}

.preference__item:nth-child(-n+3) {
  margin-bottom: 3.3333333333vw;
}

.steps {
  padding-top: 6.1458333333vw;
  padding-bottom: 5.3125vw;
}

.steps__heading {
  margin-bottom: 0.4166666667vw;
}

.steps__desc {
  margin-bottom: 3.9583333333vw;
}

.steps__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 78.6979166667vw;
  padding-right: 10.2604166667vw;
  margin: 0 auto;
  box-sizing: content-box;
}

.steps__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.steps__item__number {
  font-weight: 500;
  font-size: 2.0833333333vw;
  line-height: 1.6666666667vw;
  background: var(--gradient-main);
  -webkit-background-clip: text;
  color: transparent;
}
.steps__item__desc {
  width: 68.4375vw;
  padding: 1.4583333333vw 0;
  border-bottom: 0.1041666667vw solid var(--color-blue);
}
.steps__item:nth-child(1) .steps__item__desc {
  border-top: 0.1041666667vw solid var(--color-blue);
}

.trade {
  position: relative;
  width: 100%;
  height: 29.1666666667vw;
}

.trade__bg {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--gradient-main);
  transform: rotate(-180deg);
  border-radius: 0.8333333333vw;
  z-index: -2;
}

.trade__man {
  position: absolute;
  top: 0;
  right: 22.3958333333vw;
  width: 20.3125vw;
  height: 29.1666666667vw;
  z-index: -1;
}

.trade__heading {
  max-width: 31.9791666667vw;
  padding-top: 6.25vw;
  margin-bottom: 1.5625vw;
}

.trade__desc {
  margin-bottom: 3.8541666667vw;
}

.games {
  position: relative;
  padding-top: 6.1458333333vw;
  padding-bottom: 5.4166666667vw;
}

.games__heading {
  margin-bottom: 3.4375vw;
}

.card {
  width: 16.6666666667vw;
  height: 28.6458333333vw;
  border: 0.1041666667vw solid var(--color-blue);
  border-radius: 0.5729166667vw;
  margin: 0 1.0416666667vw;
}

.card__img {
  width: 16.6666666667vw;
  height: 17.7083333333vw;
  align-self: center;
}

.card__heading {
  padding: 1.09375vw 0.8333333333vw 0 0.8333333333vw;
  margin-bottom: 0.46875vw;
}

.card__desc {
  padding: 0 0.8333333333vw;
}

.card__more {
  display: inline-block;
  margin: -0.3125vw 0.8333333333vw 1.0416666667vw 8.0729166667vw;
  font-weight: 500;
  font-size: 0.8333333333vw;
  line-height: 0.9895833333vw;
  color: var(--color-blue);
}

.swiper-container {
  margin-left: 15.78125vw;
  margin-bottom: 3.3333333333vw;
}

.swiper-button-prev,
.swiper-button-next {
  position: absolute;
  left: 7.34375vw;
  width: 2.9166666667vw;
  height: 2.9166666667vw;
  border-radius: 100%;
  background: var(--color-white);
  border: 0.1041666667vw solid var(--color-blue);
}

.swiper-button-prev.swiper-button-disabled,
.swiper-button-next.swiper-button-disabled {
  opacity: 0.4;
  cursor: not-allowed;
  pointer-events: auto;
}

.swiper-button-prev::after,
.swiper-button-next::after {
  content: "";
  width: 2.9166666667vw;
  height: 2.9166666667vw;
  border-radius: 100%;
}

.swiper-button-prev::before,
.swiper-button-next::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-30%, -50%) rotate(-45deg);
  width: 1.0416666667vw;
  height: 1.0416666667vw;
  border-left: 0.1041666667vw solid var(--color-blue);
  border-top: 0.1041666667vw solid var(--color-blue);
}

.swiper-button-next::before {
  transform: translate(-70%, -50%) rotate(135deg);
}

.swiper-button-prev {
  top: 22.9166666667vw;
}

.swiper-button-next {
  top: 27.5vw;
}

.games__btn {
  width: 16.6666666667vw;
  height: 2.9166666667vw;
  color: #000;
  border: 0.1041666667vw solid var(--color-blue);
  margin: 0 auto;
}

.faq {
  padding-top: 6.1458333333vw;
  padding-bottom: 5.3125vw;
}

.faq__heading {
  margin-bottom: 3.4375vw;
}

#accordion {
  border-bottom: 0.1041666667vw solid var(--color-blue);
}

.accordion__heading {
  position: relative;
  width: 100%;
  padding: 1.3541666667vw 0;
  border-top: 0.1041666667vw solid var(--color-blue);
  cursor: pointer;
}
.accordion__heading::before, .accordion__heading::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  transition: 0.3s;
}
.accordion__heading::before {
  width: 1.6666666667vw;
  height: 1.6666666667vw;
  border-radius: 100%;
  background: var(--gradient-main);
}
.accordion__heading::after {
  right: 0.3645833333vw;
  width: 0.9375vw;
  height: 0.9375vw;
  background-image: url("../img/faq/plus.png");
  background-size: contain;
  background-repeat: no-repeat;
  z-index: 100;
  transform: translateY(-50%);
}

.accordion__heading.ui-accordion-header-active::after {
  transform: translateY(-50%) rotate(45deg);
}

.accordion__desc {
  padding: 0 0 1.3541666667vw 0;
}

.footer {
  position: relative;
  width: 100%;
  height: 10vw;
  background: var(--color-black);
}

.footer__container {
  display: flex;
  align-items: flex-start;
  padding-top: 1.9791666667vw;
}

.footer__f-list {
  font-style: normal;
  font-weight: 400;
  font-size: 0.9375vw;
  line-height: 1.25vw;
  color: var(--color-white);
}

.footer__s-list {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.footer__s-item a {
  font-weight: 500;
  font-size: 0.8333333333vw;
  line-height: 0.9895833333vw;
  color: #FFFFFF;
}

.footer__s-item:not(:last-child) {
  margin-bottom: 0.46875vw;
}

.footer__t-list {
  display: flex;
  align-items: center;
  margin-right: 11.5625vw;
}

.footer__t-item,
.footer__t-item svg {
  width: 1.6666666667vw;
  height: 1.6666666667vw;
}

.footer__t-item:not(:last-child) {
  margin-right: 1.6666666667vw;
}

.footer__privacy {
  position: absolute;
  left: 50%;
  bottom: 0.9895833333vw;
  transform: translateX(-50%);
  font-weight: 300;
  font-size: 0.9375vw;
  line-height: 1.1458333333vw;
  color: var(--color-privacy);
}

.popup {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  transition: 0.5s;
  visibility: hidden;
  opacity: 0;
}

.popup.is_active {
  visibility: visible;
  opacity: 1;
}

.popup__bg {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: var(--color-black);
  opacity: 0.7;
}

.popup__card {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -40%);
  display: block;
  width: 22.03125vw;
  height: 28.75vw;
  background: var(--gradient-popup);
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
  border-radius: 0.4166666667vw;
  z-index: 100;
  transition: 0.5s;
}

.popup__card.not_visible {
  display: none;
}

.popup.is_active .popup__card {
  transform: translate(-50%, -50%);
}

.popup__heading {
  max-width: 12.8645833333vw;
  padding: 1.7708333333vw 0 0.3125vw 1.6666666667vw;
  line-height: 100%;
}

.popup__desc {
  max-width: 14.5833333333vw;
  padding: 0 0 1.71875vw 1.6666666667vw;
}

.form__heading {
  font-weight: 500;
  font-size: 0.8333333333vw;
  line-height: 1.25vw;
  letter-spacing: 0.02em;
  color: var(--color-white);
  padding: 0 0 0.0520833333vw 1.6666666667vw;
}

.form__input {
  width: 18.6979166667vw;
  height: 2.5vw;
  padding: 0.625vw 0.8333333333vw;
  font-size: 0.9375vw;
  line-height: 1.25vw;
  letter-spacing: 0.02em;
  color: var(--color-gray-popup);
  border: none;
  border-radius: 0.4166666667vw;
  margin-left: 1.6666666667vw;
  margin-bottom: 1.6145833333vw;
}

.form__input::placeholder {
  font-size: 0.9375vw;
  line-height: 1.25vw;
  letter-spacing: 0.02em;
  color: var(--color-gray-popup);
}

.form__btn {
  width: 18.6979166667vw;
  height: 2.9166666667vw;
  border: none;
  cursor: pointer;
  margin-top: 0.4166666667vw;
  margin-left: 1.6666666667vw;
  margin-bottom: 0.625vw;
}

.form__agree {
  display: inline-block;
  max-width: 11.8229166667vw;
  font-size: 0.625vw;
  line-height: 0.8333333333vw;
  color: var(--color-white);
  text-align: center;
  margin: 0 5.1041666667vw;
}

.popup__close {
  position: absolute;
  top: 0.8333333333vw;
  right: 0.8333333333vw;
}

.popup__close,
.popup__close svg {
  width: 0.7291666667vw;
  height: 0.7291666667vw;
}

.popup__thx {
  position: relative;
  top: 50%;
  left: 50%;
  display: block;
  width: 22.03125vw;
  height: 10.8333333333vw;
  background: var(--gradient-popup);
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
  border-radius: 0.4166666667vw;
  z-index: 100;
  transform: translate(-50%, -50%);
  visibility: visible;
  opacity: 1;
  transition: 0.5s;
}

.popup__thx.not_visible {
  transform: translate(-50%, -25%);
  visibility: hidden;
  opacity: 0;
}