.footer {
  position: relative;

  width: 100%;
  height: vw(192);

  background: var(--color-black);
}

.footer__container {
  display: flex;
  align-items: flex-start;

  padding-top: vw(38);
}

.footer__f-list {
  font-style: normal;
  font-weight: 400;
  font-size: vw(18);
  line-height: vw(24);
  color: var(--color-white);
}

.footer__s-list {
  display: flex;
  flex-direction: column;

  margin: 0 auto;
}

.footer__s-item a {
  font-weight: 500;
  font-size: vw(16);
  line-height: vw(19);
  color: #FFFFFF;
}

.footer__s-item:not(:last-child) {
  margin-bottom: vw(9);
}

.footer__t-list {
  display: flex;
  align-items: center;

  margin-right: vw(222);
}

.footer__t-item,
.footer__t-item svg {
  width: vw(32);
  height: vw(32);
}

.footer__t-item:not(:last-child) {
  margin-right: vw(32);
}

.footer__privacy {
  position: absolute;
  left: 50%;
  bottom: vw(19);
  transform: translateX(-50%);

  font-weight: 300;
  font-size: vw(18);
  line-height: vw(22);
  color: var(--color-privacy);
}