.trade {
  position: relative;

  width: 100%;
  height: vw(560);
}

.trade__bg {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;

  width: 100%;
  height: 100%;

  background: var(--gradient-main);
  transform: rotate(-180deg);
  border-radius: vw(16);

  z-index: -2;
}

.trade__man {
  position: absolute;
  top: 0;
  right: vw(430);

  width: vw(390);
  height: vw(560);

  z-index: -1;
}

.trade__heading {
  max-width: vw(614);

  padding-top: vw(120);
  margin-bottom: vw(30);
}

.trade__desc {
  margin-bottom: vw(74);
}