// btn
.btn {
  display: flex;
  align-items: center;
  justify-content: center;

  width: vw(240);
  height: vw(56);

  font-weight: 500;
  font-size: vw(20);
  line-height: vw(24);
  border-radius: vw(8);
  color: var(--color-white);
}

.btn_fill {
  background: var(--color-main);

  margin-right: vw(40);
}

.btn_border {
  border: vw(3) solid var(--color-white);
}

.btn-box {
  display: flex;
  align-items: center;
}


// big heading / desc 
.big-heading {
  font-weight: 700;
  font-size: vw(48);
  line-height: vw(64);
  color: var(--color-white);
}

.big-desc {
  font-size: vw(18);
  line-height: vw(24);
  color: var(--color-white);
}


// main heading / desc
.heading {
  font-weight: 700;
  font-size: vw(40);
  line-height: vw(56);
  color: var(--color-black);
}

.desc {
  font-weight: 400;
  font-size: vw(18);
  line-height: vw(24);
  color: var(--color-gray);
}


// lower heading / desc
.low-heading {
  font-weight: 500;
  font-size: vw(20);
  line-height: vw(24);
  color: var(--color-black);
}

.low-desc {
  font-weight: 400;
  font-size: vw(18);
  line-height: vw(24);
  color: var(--color-black);
}