// btn fill / border / games__btn
.btn_fill {
  transition-property: background, color;
  transition-duration: .2s;
}

.btn_fill:hover,
.btn_fill:focus,
.btn_fill:active {
  background: transparent;
  border: vw(3) solid var(--color-main);
}

.header__btn:hover,
.header__btn:focus,
.header__btn:active {
  color: var(--color-black);
}


.btn_border {
  transition-property: background, color, border;
  transition-duration: .2s;
}

.btn_border:hover,
.btn_border:focus,
.btn_border:active {
  background: var(--color-white);
  color: var(--color-black);
}

.games__btn {
  transition-duration: .3s;
}

.games__btn:hover,
.games__btn:focus,
.games__btn:active {
  background: var(--color-blue);
  color: var(--color-white);
}


// nav
.header__container {
  overflow: hidden;
}

.nav__link {
  position: relative;
}

.nav__link::before {
  content: '';
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: vw(4);
  background: var(--color-black);
  opacity: 0;
  transform: translateY(100%);

  transition: opacity .2s, transform .3s;
}

.nav__link:hover::before,
.nav__link:focus::before,
.nav__link:active::before {
  opacity: 1;
  transform: none;
}

.nav__link.is_active::before {
  opacity: 1;
  transform: none;
}


// preference__item
.preference__item {
  transition: background .5s, box-shadow .5s;
}

.preference__item:hover {
  background: transparent;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.04);
}


// plus
.accordion__heading:hover::after {
  transform: translateY(-50%) rotate(-180deg);
}


// arrows
.swiper-button-prev::before,
.swiper-button-next::before {
  transition: .3s;
}

.swiper-button-prev:hover::before,
.swiper-button-next:hover::before {
  border-left: vw(2) solid var(--color-white);
  border-top: vw(2) solid var(--color-white);
}

.swiper-button-prev.swiper-button-disabled:hover::before,
.swiper-button-next.swiper-button-disabled:hover::before {
  border-left: vw(2) solid var(--color-blue);
  border-top: vw(2) solid var(--color-blue);
}

.swiper-button-prev,
.swiper-button-next {
  transition: .3s;
}

.swiper-button-prev:hover,
.swiper-button-next:hover {
  background: var(--color-blue);
}

.swiper-button-prev.swiper-button-disabled:hover,
.swiper-button-next.swiper-button-disabled:hover {
  background: var(--color-white);
}


// popup input
.form__input {
  transition: .3s;
}

.form__input:focus,
.form__input:active {
  color: var(--color-black);
  outline: none;
}


// select
.header *::selection,
.hero *::selection,
.trade *::selection {
  background: var(--color-main);
  color: var(--color-black);
}

.preference *::selection,
.steps *::selection,
.games *::selection,
.faq *::selection,
.footer *::selection {
  background: var(--color-turquoise);
  color: var(--color-black);
}


// scrollbar
*::-webkit-scrollbar {
  width: 12px;               /* ширина scrollbar */
}
*::-webkit-scrollbar-track {
  background: transparent;        /* цвет дорожки */
}
*::-webkit-scrollbar-thumb {
  background: var(--color-main);    /* цвет плашки */
  border-radius: vw(20);       /* закругления плашки */
  border: vw(3) solid var(--color-white);  /* padding вокруг плашки */
}